body {
    margin: 0px;
}
.carousel{
    width: 100%;
    overflow: scroll;
    padding: 0px;
    position: relative;

}
.carousel::-webkit-scrollbar{
    display: none;
}
.carousel__container{
    white-space: nowrap;
    margin: 10px 0px;
    padding-bottom: 2%;
}

.Card{
    background-color: #edf2f4;
    height:8rem;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    display: inline-block;
    margin-right: 10px;
    margin-top: 25px;
    cursor: pointer;
    transition: 450ms all;
    -webkit-transform-origin: center left;
    transform-origin: center left;
}
.Card:hover ~ .Card{
    -webkit-transform: translate3d(100px, 0, 0);
    transform: translate3d(100px, 0, 0);
}
.Card:hover{
    opacity: 1;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.CardImage{
    height: 80%;
    object-fit: cover;
}
.CardBody{
    height: auto;
    margin: 0px;
    padding: 0px;
}
.CardSubtitle{
    margin-top: 4%;
    text-align: center;
}
.Cardlink{
    text-decoration: none;
}


  
.bodyFoodDay{
    display: flex;
    justify-content: space-between;
}
.foodBlock{
    width: auto;
    height: 100%;
    padding: 2% 10%;
    position: relative; 

}
.foodBlock_title{
    text-align: center;
    border-bottom: solid #BDBDBD 2px;
    color: #666;
}
.RecipeofDay{
    -webkit-transform-origin: none;
    transform-origin: none;
    position: absolute;
    left:50%;
    -webkit-transform:translate(-50%,-50%);
            transform:translate(-50%,-50%);
}
.RecipeofDay:hover{
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
